/* eslint-disable */
import React from "react";
import inputChecker from "./inputChecker";
import { ADD_ITEM } from "../../actions/messagesActions";
import { connect } from "react-redux";

const mapDispatchToProps = dispatch => {
	return {
		ADD_ITEM: item => {
			dispatch(ADD_ITEM(item))
		}
	};
}

class FeedbackSection extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			name: "",
			phone: "",
			email: "",
			message: "",
			validated: {
				name: undefined,
				phone: undefined,
				email: undefined,
				message: undefined
			},
			selectedFile: null,
			loaded: 0
		};

	}

	handleInputValue = (event) => {
		let { name, value } = event.currentTarget;
		this.setState({
			[name]: value
		});
	};

	handleSubmit = (event) => {
		event.preventDefault();
		let { name, phone, email, message } = this.state;
		this.setState({
			validated: {
				name: inputChecker.name(name),
				phone: inputChecker.phone(phone),
				email: inputChecker.email(email),
				message: inputChecker.message(message)
			}
		}, () => {
			for (let key in this.state.validated) {
				if (!this.state.validated[key]) return;
			}

			const item = {
				date: new Date(),
				name,
				phone,
				email,
				message
			};
			this.props.ADD_ITEM(item);
			this.setState({
				name: "",
				phone: "",
				email: "",
				message: "",
				validated: {
					name: undefined,
					phone: undefined,
					email: undefined,
					message: undefined
				}
			}, () => alert("Сообщение отправлено!"));
		})

	};

	showError = (validated) => {
		return validated === false ? 'd-block' : 'd-none';
	};

	render() {
		let { name, phone, email } = this.state.validated;
		return (
			<section className="feedback-section mt-20 pt-24 pb-30 px-15 flex-column flex-align-center">

				<span className="title">Обратная связь</span>

				<form className="mt-15" onSubmit={this.handleSubmit}>
					<div className="input-group flex-space-between">
						<label className="label label-1 flex-column flex-align-center">
							<span className="title mb-8">Имя</span>
							<input placeholder="Ваше имя" type="text" name="name" onChange={this.handleInputValue}/>
							<div className={`error-text mt-10 flex-column flex-align-center ${this.showError(name)}`}>
								<div className="error error-1 mb-5">
									Длинна строки от 2 до 50 символов
								</div>
								<div className="error error-2">
									Допустимые символы: (, . - ')
								</div>
							</div>
						</label>
						<label className="label label-2 flex-column flex-align-center">
							<span className="title mb-8">Телефон</span>
							<input placeholder="+x (xxx) xxx-xx-xx" type="text" name="phone" onChange={this.handleInputValue}/>
							<div className={`error-text mt-10 flex-column flex-align-center ${this.showError(phone)}`}>
								<div className="error error-1 mb-5">
									Длинна строки от 8 до 20 символов
								</div>
								<div className="error error-2">
									Допустимые символы: (+ - ( ) )
								</div>
							</div>
						</label>
						<label className="label label-3 flex-column flex-align-center">
							<span className="title mb-8">E-mail</span>
							<input placeholder="Ваш e-mail" type="text" name="email" onChange={this.handleInputValue}/>
							<div className={`error-text mt-10 flex-column flex-align-center ${this.showError(email)}`}>
								<div className="error error-1 mb-5">
									Почта введена неверно
								</div>
							</div>
						</label>
					</div>
					<label className="label label-4 flex-column flex-align-center">
						<span className="title mb-8">Сообщение</span>
						<textarea placeholder="Текст сообщения" name="message" rows="10" onChange={this.handleInputValue} />
						<div className={`error-text flex-column flex-align-center`}></div>
					</label>
					<button className="submit-btn centered pointer" type="submit">
						<span>Отправить</span>
					</button>
				</form>

			</section>
		);
	}
}

export default connect(null, mapDispatchToProps)(FeedbackSection)