/* eslint-disable */
import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { connect } from 'react-redux';
import { SWITCH_LANGUAGE } from "../actions/langActions";

import ImgLogo from "../assets/logo.png";

import {ReactComponent as IconBurgerMenu} from "../assets/burger-menu.svg";
import {ReactComponent as IconAdmin} from "../assets/admin-with-cogwheels.svg";

const mapStateToProps = state => {
	return {
		lang: state.lang.lang,
		loggedIn: state.auth.loggedIn
	}
};

const mapDispatchToProps = dispatch => {
	return {
		SWITCH_LANGUAGE: language => {
			dispatch(SWITCH_LANGUAGE(language))
		}
	};
};


class MobileNavbar extends Component {
	constructor(props) {
		super(props);

		this.state = {
			menu_hidden: true
		};
	}

	// switchLanguage = () => {
	// 	let language = this.props.lang === "RU" ? "EN" : "RU";
	// 	this.props.SWITCH_LANGUAGE(language)
	// };

	renderMenu = () => {
		return !this.state.menu_hidden
			? <>
				<li className="item item-2" onClick={this.toggleMenu}><Link className={'centered'} to='/'>О компании</Link></li>
				<li className="item item-3" onClick={this.toggleMenu}><Link className={'centered'} to='/info'>Продукция и направление работ</Link></li>
				<li className="item item-4" onClick={this.toggleMenu}><Link className={'centered'} to='/contacts'>Контакты</Link></li>
			</>
			: null
	};

	toggleMenu = () => {
		this.setState({
			menu_hidden: !this.state.menu_hidden
		});
	};

	render() {
		let { lang, loggedIn } = this.props;
		let langBtnText = lang === "RU" ? "EN" : "RU";
		// TODO: включить кнопки, доделать переводы
		let admin_btn_visibility = loggedIn ? {visibility: "visible"} : {visibility: "hidden"};
		let burger_btn_style = !this.state.menu_hidden ? {fill: "#c9a87b"} : {};

		return (
			<nav className='mobile-navbar fixed'>
				<ul className="menu flex-column flex-align-center p-10">
					<li className="item item-1 flex-space-between">
						<button className={'toggle-btn pointer'} onClick={this.toggleMenu}><IconBurgerMenu className={"h-40 w-40"} style={burger_btn_style} /></button>
						<div className={'logo flex-justify-center'}><img className={'h-40'} src={ImgLogo} /></div>
						<Link className={'admin-btn pointer'} style={admin_btn_visibility} to='/admin'><IconAdmin className={"h-40 w-40"} /></Link>
					</li>
					{this.renderMenu()}
				</ul>
			</nav>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(MobileNavbar);



