/* eslint-disable */
import React from 'react';
import Picture_1 from "../assets/homepage-pic-1.png";
import Picture_2 from "../assets/homepage-pic-2.png";
import BgImage from "../assets/background.jpg";
import Logo from "../assets/logo.png";


class Homepage extends React.Component {
	constructor(props) {
		super(props);

		this.state = {};

	}

	render() {
		return (
			<section className="homepage-section flex-column flex-align-center">
				<header className="relative">
					<div className="background">
						<div className="bg-shadow absolute"></div>
						<div className="bg-image" style={{backgroundImage: `url(${BgImage})`}}></div>
					</div>
					<img className="logo absolute" src={Logo}/>
				</header>
				<div className="main-content mt-50 mb-70 px-30">
					<div className="text-block">
						ООО «Дельта Р» - научно–производственная компания. Образована в 2008 г. Основное направление деятельности: производство и применение противотурбулентных присадок для нефте и газо продуктов. Изначально упор был сделан на разработку такой технологии и продукта, которые построены  на использовании преимущественно отечественного сырья и оборудования. Это обеспечивает независимость производства от западных поставщиков сырья и технологий.
						Компания работает в сотрудничестве с Российской Академией Наук.
						В настоящее время завершена разработка и  запущен в производство первый в линейке продуктов – противотурбулентная присадка, торговая марка «Нара». Пройденные опытно-промысловые испытания показывают высокую эффективность данного продукта.
						Головной офис и лабораторный центр ООО «Дельта – Р» расположены в г. Москве.
						В 2018 г. запущено производство в г. Тольятти.
					</div>
					<div className="img-block ml-30">
						<img className="img img-1" src={Picture_1}/>
						<img className="img img-2" src={Picture_2}/>
					</div>
				</div>
			</section>
		);
	}
}

Homepage.propTypes = {};

export default Homepage;