/* eslint-disable */
import React from 'react';
import IconGmail from "../assets/gmail.svg";
import IconYoutube from "../assets/youtube.svg";
import IconLinkedin from "../assets/linkedin.svg";
import {Link} from "react-router-dom";


const Footerbar = () => {
	return (
		<footer className="footerbar flex-align-center flex-space-evenly">
			<span className="copyright">© 2018 Faraks-01</span>
			<ul className="menu flex-align-center mx-10">
				{/*<li className="item item-1"><Link exact to='/'>На главную</Link></li>*/}
				<li className="item item-2"><Link to='/'>О компании</Link></li>
				<li className="item item-3"><Link to='/info'>Продукция и направление работ</Link></li>
				<li className="item item-3"><Link to='/contacts'>Контакты</Link></li>
			</ul>

			<ul className="soc-logo-list flex-align-center">
				<li className="logo logo-1">
					<a href="https://mail.google.com/mail/" target="_blank" rel="noopener noreferrer">
						<img className="w-28 h-28 " src={IconGmail}/>
					</a>
				</li>
				<li className="logo logo-2">
					<a href="https://www.youtube.com/" target="_blank" rel="noopener noreferrer">
						<img className="w-28 h-28 " src={IconYoutube}/>
					</a>
				</li>
				<li className="logo logo-3">
					<a href="https://ru.linkedin.com/" target="_blank" rel="noopener noreferrer">
						<img className="w-28 h-28 " src={IconLinkedin}/>
					</a>
				</li>
			</ul>
		</footer>
	);
};

export default Footerbar;
