import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { connect } from 'react-redux';
import { SWITCH_LANGUAGE } from "../actions/langActions";


import IconMagnifier from "../assets/magnifier.svg";
import {ReactComponent as IconAdmin} from "../assets/admin-with-cogwheels.svg";

const mapStateToProps = state => {
	return {
		lang: state.lang.lang,
		loggedIn: state.auth.loggedIn
	}
};

const mapDispatchToProps = dispatch => {
	return {
		SWITCH_LANGUAGE: language => {
			dispatch(SWITCH_LANGUAGE(language))
		}
	};
};

class Navbar extends Component {

	switchLanguage = () => {
		// let language = this.props.lang === "RU" ? "EN" : "RU";
		// this.props.SWITCH_LANGUAGE(language)
	}

	render() {
		let { lang, loggedIn } = this.props;
		let langBtnText = lang === "RU" ? "EN" : "RU";

		// TODO: включить кнопки, доделать переводы
		let adminBtnVisibility = loggedIn ? {display: "list-item"} : {display: "none"};

		return (
			<nav className='navbar fixed'>
				<ul className="menu flex-space-evenly flex-align-center">
					<li className="item item-1"><Link to='/'>О компании</Link></li>
					<li className="item item-2"><Link to='/info'>Продукция и направление работ</Link></li>
					<li className="item item-3"><Link to='/contacts'>Контакты</Link></li>
					{/*<li className="item item-4">*/}
					{/*	<span className="pointer select-none" onClick={this.switchLanguage}>*/}
					{/*		{langBtnText}*/}
					{/*	</span>*/}
					{/*</li>*/}
					{/*<li className="item item-5">*/}
					{/*	<label className="search flex-align-center">*/}
					{/*		<input type="text" />*/}
					{/*		<button className="centered pointer select-none out-none">*/}
					{/*			<img className="w-24 h-24" src={IconMagnifier}/>*/}
					{/*		</button>*/}
					{/*	</label>*/}
					{/*</li>*/}
					<li className="item item-6" style={adminBtnVisibility}><Link to='/admin'><IconAdmin className="h-40 w-40" /></Link></li>
				</ul>
			</nav>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);



