/* eslint-disable */
import React, { Component } from 'react';
import '../stylesheets/App.scss';
import { Switch, Route, withRouter } from 'react-router-dom'
import { connect } from 'react-redux';

import { CHECK_AUTH } from "../actions/authActions";


import Navbar from "./Navbar";
import Footerbar from "./Footerbar";
import Homepage from "./Homepage";
import Contacts from "./Contacts/index";
import Admin from "./Admin/index";
import InfoPage from "./InfoPage";
import OldBrowsersStub from "./OldBrowsersStub";
import MobileNavbar from "./MobileNavbar";
import NotFoundPage from "./NotFoundPage";

const mapStateToProps = state => {
	return {
		browserName: state.userAgent.browserName
	}
};

const mapDispatchToProps = dispatch => {
	return {
		CHECK_AUTH: () => {
			dispatch(CHECK_AUTH())
		}
	};
};

class App extends Component {

	componentDidMount = () => {
		this.props.CHECK_AUTH();
	};

	render() {
		let { browserName } = this.props;

		return (
			<div className="app flex-column h-inherit relative">
				<Navbar />
				<MobileNavbar />
				<main>
					<Switch>
						<Route exact path="/" component={Homepage} />
						<Route exact path="/contacts" component={Contacts} />
						<Route exact path="/info" component={InfoPage} />
						<Route path="/admin" component={Admin}/>
						<Route component={NotFoundPage} />
					</Switch>
				</main>
				<Footerbar />
			</div>
		)
	};

};

export default withRouter(
	connect(mapStateToProps, mapDispatchToProps)(App)
)
