/* eslint-disable */
import React from 'react';
import IconWorldmap from "../../assets/worldmap.svg";
import {ReactComponent as IconClocks} from "../../assets/clocks.svg";
import {ReactComponent as IconSmartphone} from "../../assets/smartphone.svg";
import {ReactComponent as IconEnvelope} from "../../assets/envelope.svg";

class InfoSection extends React.Component {
	constructor(props) {
		super(props);

		this.state = {};

	}

	render() {
		return (
			<section className="info-section flex-column flex-align-center px-15">
					<span className="title mt-19">Контактная информация</span>
					<span className="text mt-14">Если вы заинтересованы или есть какие-либо вопросы, пожалуйста, свяжитесь с нами.</span>
					<hr />
					<ul className="card-list">
						<li className="card card-1 flex-column flex-align-center">
							<div className="icon-wrapper p-14">
								<div className="worldmap-icon-wrapper w-82 h-82">
									<div className="img-group">
										<img className="w-142 h-79 icon-1" src={IconWorldmap} />
										<img className="w-142 h-79 icon-2" src={IconWorldmap} />
									</div>
								</div>
							</div>
							<span className="title mt-13">АДРЕС</span>
							<hr className="my-15" />
							<span className="text text-1">Россия, г. Тольятти,</span>
							<span className="text text-2 mt-5">ул. Новозаводская, 8</span>
						</li>
						<li className="card card-2 flex-column flex-align-center">
							<div className="icon-wrapper w-80 h-80 p-14">
								<IconClocks />
							</div>
							<span className="title mt-13">ГРАФИК</span>
							<hr className="my-15" />
							<span className="text text-1">Пн-Пт: c 9.00 - 19.00</span>
							<span className="text text-2 mt-5">Сб-Вс: с 10.00 - 16.00</span>
						</li>
						<li className="card card-3 flex-column flex-align-center">
							<div className="icon-wrapper w-95 h-88 p-6">
								<IconSmartphone />
							</div>
							<span className="title mt-13">ТЕЛЕФОНЫ</span>
							<hr className="my-15" />
							<span className="text text-1">+7(123) 456-78-90</span>
							<span className="text text-2 mt-5">+7(098) 765-43-21</span>
						</li>
						<li className="card card-4 flex-column flex-align-center">
							<div className="icon-wrapper w-64 h-90 p-20">
								<div>
									<IconEnvelope />
								</div>
							</div>
							<span className="title mt-13">E-MAIL</span>
							<hr className="my-15" />
							<span className="text text-1">example@gmail.com</span>
							<span className="text text-2 mt-5">example@yandex.ru</span>
						</li>
					</ul>
				</section>
		);
	}
}

InfoSection.propTypes = {};

export default InfoSection;